.social-media {
  width: 116px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.ham {
  display: none;
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: transparent;
  transition: transform 0.2s linear;
}

.ham:hover {
  transform: translateY(-1px);
}

.ham:active {
  transform: translateY(1px);
}

@media screen and (max-width: 991px) {
  .ham {
    display: block;
  }
}

.social-media > a:hover {
  transform: translateY(-2px);
}